import React, {Component} from 'react';
import Select from 'react-select';
import _ from 'lodash';

import 'bootstrap/dist/css/bootstrap.css';
import './MentorSelect.css';

import Table from '../Table/Table';
import data from '../data.json';

class MentorSelect extends Component {
  state = {
    mentorFocused: localStorage.getItem("chosenMentor")
  }

  render() {
    const mentors = _.map(data, mentor => mentor.github);
    const options = [];
    
    _.forEach(mentors, mentor => {
      const mentorID = _.replace(mentor, 'https://github.com/', '');

      options.push({value: mentorID, label: mentorID});
    });

    let table = null;
    let tableName = null;

    if (this.state.mentorFocused) {
      localStorage.setItem("chosenMentor", this.state.mentorFocused);

      table = <Table mentorGithub={this.state.mentorFocused} />;
      tableName = <h4>{this.state.mentorFocused}'s mentees</h4>
    }

    return (
      <div className='container'>
        <div className='select-container'>
          <Select className='mentor-select'
                  options={options}
                  onChange={this.setFocusedMentor}
                  theme={(theme) => ({
                    ...theme,
                    borderRadius: 0,
                    colors: {
                    ...theme.colors,
                      primary25: '#d69dff',
                      primary: '#000000',
                    },
                  })}
                  placeholder="Enter mentor's github..."
                  autoFocus
          />
          {tableName}
        </div>
        {table}
      </div>
    )
  }

  setFocusedMentor = (e) => this.setState ({
    mentorFocused: e.value
  });
};

export default MentorSelect;