import React, {Component} from 'react';
import _ from 'lodash';

import 'bootstrap/dist/css/bootstrap.css';
import './Table.css';

import data from '../data.json';

class Table extends Component {
  render() {
    const {mentorGithub} = this.props;
    const fullGithubName = 'https://github.com/' + mentorGithub;

    const mentor = _.find(data, {github: fullGithubName});

    if (_.isEmpty(mentor.students)) return <p>No students to show</p>;

    //create table
    const headers = _.map(mentor.students, student => <th key={student.name}>{student.name}</th>);
    
    const tasks = mentor.students[0].tasks;
    const body = _.map(tasks, task => (
      <tr key={task.taskName}>
        <td>{task.taskName}</td>
        {_.map(mentor.students, student => {
          const currentTask = _.find(student.tasks, {taskName: task.taskName});
          let styles = null;

          switch(currentTask.status.toLowerCase()) {
            case "checked": 
              styles = {background: "#ADFF2F"};
              break;
            case "checking": 
              styles = {background: "#FFB6C1"};
              break;
            case "in progress": 
              styles = {background: "#FFD700"};
              break;
            case "no mark": 
              styles = {background: "#FF6347"};
              break;
            case "todo": 
              styles = {background: "#DCDCDC"};
              break;
            default:
              break; 
          }

          return <td data-status={currentTask.status} style={styles} key={task.taskName+student.name}>{currentTask.status}</td>;
        })}
      </tr>
    ));

    return (
      <table className='mentee-table'>
				<thead>
          <tr>
            <th></th>
            {headers}
          </tr>
				</thead>
				<tbody>
          {body}
				</tbody>
			</table>
    );
  }
}

export default Table;